/**
 * @file 全部礼包
 */
@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';
@import '../../assets/styles/core/common';
.dealer_list_container {
  // padding: 0 pr2(20);
  .car_info_wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    padding: pr2(12) pr2(20) pr2(13);
    box-sizing: border-box;
    background: #fff;
    &.padding_top {
      top: pr2(44);
    }
    .cell {
      padding: 0 pr2(20);
      box-sizing: border-box;
      height: pr2(50);
      display: flex;
      justify-content: flex-start;
      align-items: center;
      color: #111E36;
      box-shadow: 0 pr2(4) pr2(20) 0 rgba(17, 30, 54, .06);
      border-radius: pr2(4);
      .bd {
        flex: 1;
        @include ellipsis();
        font-size: pr2(18);
        font-family: PingFangSC-Medium,PingFang SC;
        font-weight: 500;
      }
      .ft {
        margin-left: pr2(4);
        width: pr2(12);
        height: pr2(12);
        background: url('data:image/png;base64,') center center no-repeat;
        background-size: 100%;
      }
    }
  }
  .date_wrapper {
    position: fixed;
    top: pr2(0);
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: #fff;
    padding: pr2(11) pr2(55) pr2(13) pr2(20);
    &.padding_top {
      top: pr2(56);
    }
    &.header_maintain_padding {
      top: pr2(74);
    }
    &:after {
      @include borderBottom(#F1F3F6);
    }
    .slide_wrapper {
      width: 100%;
      overflow-y: hidden;
      overflow-x: auto;
      &::-webkit-scrollbar {
        display: none;
      }
      .slide_box {
        white-space: nowrap;
        .date_item {
          display: inline-block;
          padding: pr2(8) 0;
          width: pr2(61);
          text-align: center;
          color: #111E36;
          &.active {
            color: #fff;
            background-color: #0055FF;
            border-radius: pr2(12) 0 pr2(12) 0;
          }
          .date {
            font-size: pr2(14);
            line-height: pr2(20);
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
          }
          .week {
            font-size: pr2(12);
            line-height: pr2(17);
          }
        }
      }
    }

    .icon_calendar {
      position: absolute;
      top: pr2(11);
      right: pr2(20);
      width: pr2(25);
      height: pr2(54);
      background: url('data:image/png;base64,') right center no-repeat;
      background-size: pr2(25) pr2(24);
    }
  }
  .dealer_filter {
    position: fixed;
    top: pr2(74);
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    &.padding_top {
      top: pr2(130);
    }
    &.header_maintain_padding {
      top: pr2(148) !important;
    }
    .filter_item {
      padding: pr2(14) 0;
      font-size: pr2(14);
      color: #191A1A;
      line-height: pr2(16);
      font-family: PingFangSC-Medium,PingFang SC;
      font-weight: 500;
      .current {
        display: flex;
        justify-content: center;
        align-items: center;
        .icon_arrow_down {
          display: block;
          margin-left: pr2(4);
          width: pr2(7);
          height: pr2(4);
          background: url('data:image/png;base64,') center center no-repeat;
          background-size: 100%;
        }
        .icon_arrow_up {
          display: block;
          margin-left: pr2(4);
          width: pr2(7);
          height: pr2(4);
          background: url('data:image/png;base64,') center center no-repeat;
          background-size: 100%;
        }
      }
      .drop_down {
        position: fixed;
        top: pr2(116);
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1000;
        background-color: rgba(0, 0, 0, .6);
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight: 400;
        font-size: pr2(15);
        color: #111E36;
        line-height: pr2(50);
        text-align: center;
        &.padding_top {
          top: pr2(184);
        }
        &.header_maintain_padding {
          top: pr2(190) !important;
        }
        // 选择区域
        .zone_model, .sort_model{
          position: relative;
          height: pr2(362);
          background-color: #fff;
          overflow: hidden;
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          &:after {
            @include borderTop(#E6EBF5);
          }
        }
        .zone {
          width: pr2(130);
          height: 100%;
          overflow-y: auto;
          background-color: #FAF8FC;
          &::-webkit-scrollbar {
            display:none;
          }
          ul li.active {
            color: #0055FF;
            background-color: #fff;
          }
        }
        .zoneFilter {
          flex: 1;
          height: 100%;
          overflow-y: auto;
          background-color: #fff;
          &::-webkit-scrollbar {
            display:none;
          }
          ul li.active {
            color: #0055FF;
          }
        }
        // 选择排序
        .sort {
          width: 100%;
          height: 100%;
          overflow-y: auto;
          &::-webkit-scrollbar {
            display:none;
          }
          ul li.active {
            color: #0055FF;
          }
        }
      }
      &.active .current {
        color: #0051FF;
      }
    }
  }
  .dealer_list_wrapper {
    width: 100%;
    padding: pr2(120) pr2(20) 0;
    box-sizing: border-box;
    overflow-y: auto;
    &.padding_top {
      padding-top: pr2(180);
    }
    .dealer_list {
      .dealer_item {
        position: relative;
        &.is_activity_or_package{
          padding-bottom: pr2(16);
        }
        &:after {
          @include borderBottom(#E6EBF5);
        }
        .dealer_wrapper {
          padding: pr2(12) 0;
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          .img_box {
            margin-right: pr2(12);
            position: relative;
            width: pr2(110);
            height: pr2(84);
            border-radius: pr2(4);
            overflow: hidden;
            &.is_transparent:before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              z-index: 0;
              width: pr2(58);
              height: pr2(18);
              background: #0055FF;
            }
            img {
              width: pr2(110);
              height: pr2(84);
            }
            .orange_tag {
              position: absolute;
              top: pr2(3);
              left: pr2(3);
              z-index: 1;
              width: pr2(60);
              height: pr2(18);
              background: #FF6600;
              border-radius: 0 pr2(4) 0 pr2(4);
              font-size: pr2(12);
              color: #fff;
              font-family: PingFangSC-Semibold,PingFang SC;
              font-weight: 600;
              text-align: center;
              line-height: pr2(18);
            }
          }
          .dealer_info {
            position: relative;
            flex: 1;
            width: pr2(213);
            height: pr2(84);
            .name {
              margin-bottom: pr2(5);
              font-size: pr2(16);
              color: #111E36;
              line-height: pr2(18);
              @include ellipsis();
              .tag_4s {
                vertical-align: middle;
                margin-right: pr2(5);
                @extend %tag-4s;
              }
            }
            .stars {
              margin-bottom: pr2(7);
              height: pr2(16);
              display: flex;
              justify-content: space-between;
              align-items: center;
              .price {
                font-size: pr2(12);
                color: #666D7F;
                line-height: pr2(16);
                font-weight: 600;
                font-family: PingFangSC-Semibold,PingFang SC;
                span {
                  font-size: pr2(16);
                  color: #FF6600;
                  line-height: 1;
                }
              }
            }
            .address {
              margin-bottom: pr2(6);
              display: flex;
              align-items: center;
              justify-content: flex-start;
              font-size: pr2(12);
              color: #666D7F;
              line-height: 1;
              height: pr2(12);
              .area {
                margin-right: pr2(20);
                padding-left: pr2(14);
                background: url('data:image/png;base64,') left center no-repeat;
                background-size: auto pr2(12);
                @include ellipsis();
                width: pr2(51);
              }
              .distance {

              }
            }
            .activity_list {
              display: flex;
              justify-content: flex-start;
              align-items: center;
              .activity_item {
                padding: 0 pr2(4);
                margin-right: pr2(4);
                font-size: pr2(11);
                color: #FF6600;
                line-height: pr2(17);
                background: rgba(255, 102, 0, .1);
                border-radius: pr2(4) 0 pr2(4) 0;
                &:last-child {
                  margin-right: 0;
                }
              }
            }
            .btn_book {
              position: absolute;
              bottom: 0;
              right: 0;
              width: pr2(64);
              height: pr2(32);
              border-radius: pr2(4);
              background-color: #0055FF;
              font-size: pr2(12);
              color: #fff;
              line-height: pr2(32);
              text-align: center;
            }
          }
        }
        .package_item {
          margin-bottom: pr2(12);
          padding: 0 pr2(13) 0 pr2(8);
          display: flex;
          justify-content: flex-start;
          align-items: center;
          height: pr2(44);
          box-shadow: 0 pr2(4) pr2(20) 0 rgba(17, 30, 54, 0.06);
          font-family: PingFangSC-Regular,PingFang SC;
          border-radius: pr2(4);
          &:last-child {
            margin-bottom: 0;
          }
          .sort {
            margin-right: pr2(10);
            padding: pr2(3) pr2(4);
            font-size: pr2(11);
            color: #0055FF;
            line-height: 1;
            background-color: rgba(0, 85, 255, 0.06);
            border-radius: pr2(4) 0 pr2(4) 0;
          }
          .name {
            flex: 1;
            font-size: pr2(14);
            color: #666D7F;
            line-height: pr2(20);
            @include ellipsis();
          }
          .detail {
            margin-left: pr2(10);
            padding-left: pr2(8);
            padding-right: pr2(16);
            font-size: pr2(12);
            color: #0055FF;
            line-height: pr2(18);
            font-weight: 300;
            border: 1px solid rgba(0, 85, 255, .2);
            border-radius: pr2(10);
            box-sizing: border-box;
            background: url('data:image/png;base64,') right pr2(5) center no-repeat;
            background-size: pr2(8) pr2(8);
          }
        }
      }
    }
  }
}